import request from '@/utils/request'
// import qs from 'qs'


// 用户登录
export function login(data) {
    return request({
        method: 'post',
        url: '/api/authorizations',
        // data: {
        //     email: data.email,
        //     password: data.password
        // }
        headers: {
            "Content-type": "multipart/form-data"
        },
        data: data
    })
}

// token刷新
export function refreshToken(newToken) {
    return request({
        method: 'put',
        url: '/api/authorizations/current',
        headers: { 'Authorization': 'Bearer' + newToken },
    })
}

// token删除
export function logout(newToken) {
    return request({
        method: 'delete',
        url: '/api/authorizations/current',
        headers: {
            'Authorization': 'Bearer' + newToken,
            "Content-type": "multipart/form-data"
        },
    })
}

// 单个id公司信息
export function searchCompanyById(id) {
    return request({
        method: 'get',
        url: '/api/search',
        params: {
            id: id
        }
    })
}

// 公司名搜索
export function searchCompany(newToken, data) {
    return request({
        method: 'post',
        url: '/api/search',
        headers: { 'Authorization': 'Bearer' + newToken },
        data: data
    })
}

// csv数据更新
export function csv_search(newToken, data) {
    return request({
        method: 'put',
        url: '/api/csv-update',
    })
}

// 非匹配数据下载
export function csv_unconfirm(newToken, data) {
    return request({
        method: 'post',
        url: '/api/csv-unconfirm',
    })
}


// CSV数据下载
export function csv_upload(newToken, data) {
    return request({
        method: 'post',
        url: '/api/csv-upload',
    })
}

// 匹配数据下载
export function csv_confirm(newToken, data) {
    return request({
        method: 'post',
        url: '/api/csv-confirm',
    })
}






