<template>
  <div class="sitemap_container">
    <div class="logo_img">
      <span class="logo_text">sitemap</span>
    </div>
    <div class="login_content">
      <div class="message_notice" :style=" 'color:' + fontColor ">{{messageContent}}</div>
      <div class="login_form">
        <h2>GOMINE内部数据管理系统V1.0</h2>
        <p>
          <span>账号：</span>
          <input type="text" placeholder="请输入公司邮箱" v-model="emailText" />
        </p>
        <p>
          <span>密码：</span>
          <input type="password" placeholder="请输入密码" v-model="psdText" />
        </p>
        <p class="submit_p">
          <button class="submit_bth" @click="submitMessage()">提交</button>
        </p>
      </div>
    </div>
  </div>
</template>
<script>
import { login } from "@/api";
export default {
  name: "Login",
  data() {
    return {
      messageIshow: false,
      messageContent: "",
      fontColor: "",
      emailText: "",
      psdText: "",
      data: null
    };
  },
  methods: {
    setIsshow(message, fontColor) {
      this.messageIshow = true;
      this.messageContent = message;
      this.fontColor = fontColor;
      setTimeout(() => {
        this.messageIshow = false;
      }, 2000);
    },
    submitMessage() {
      let vm = this;
      //console.log("aaaa")
      if (this.emailText == "") {
        this.setIsshow("请输入邮箱账号！", "#e6a23c");
      } else if (this.psdText == "") {
        this.setIsshow("请输入密码！", "#e6a23c");
      } else {
        // vm.$commonTools
        //   .checkToken()
        //   .then(function(res) {
        //     let token = vm.$commonTools.getCookie("user_token");
        //     let newToken = token.replace('"', "").replace('"', "");
        //     console.log(token);

        var formData = new FormData();
        var formData = new window.FormData();
        formData.append("email", vm.emailText);
        formData.append("password", vm.psdText);
        vm.data = formData;

        login(vm.data)
          .then(function(response) {
            if (response.status === 201) {
              let access_token = response.data.access_token
              // console.log(response)
              // 对比成功
              vm.$commonTools.setCookie(
                "user_token",
                JSON.stringify(access_token),
                60
              );
              vm.$router.push({ path: "/dataUpload" });
              console.log("201");
            } else {
              console.log("Failed");
              this.setIsshow("登录失败，请重试！", "#e6a23c");
            }
          })
          .catch(function(error) {
            console.log(error);
            console.log("Failed");
            vm.setIsshow("登录失败，请重试！", "#e6a23c");
          });
        // })
        // .catch(function(error) {
        //   console.log(error);
        // });
      }
    }
  }
};
</script>
<style scoped>
.sitemap_container {
  width: 100%;
  height: 100vh;
  overflow: hidden;
}
/* logo开始 */
.logo_img {
  width: 75%;
  min-width: 1200px;
  height: 10vh;
  line-height: 10vh;
  margin: 0 auto;
  text-align: left;
  color: #0e63af;
  margin-bottom: 50px;
}
.logo_text {
  color: #0e63af;
  display: block;
  font-size: 30px;
  font-weight: 700;
}
/* logo结束 */

/* 信息提示 */
.message_notice {
  width: 450px;
  height: 20px;
  line-height: 20px;
  margin: 0 auto;
}

/* 表单开始 */
.login_content {
  position: relative;
  width: 1200px;
  height: 80vh;
  margin: 0 auto;
}
.login_form {
  width: 450px;
  margin: 10px auto;
  text-align: center;
  padding: 20px;
  border-radius: 2px;
  padding: 25px 10px 40px 10px;
  border: 1px solid #f0f0f0;
  -moz-box-shadow: 0px 0px 7px #f0f0f0;
  -webkit-box-shadow: 0px 7px 5px #f0f0f0;
  box-shadow: 0px 0px 7px #f0f0f0;
}
/* .login_form:hover {
  box-shadow: 0px 0px 4px #333333;
} */
.login_form h3 {
  margin-bottom: 20px;
}
.login_form p {
  height: 45px;
  line-height: 45px;
}
.login_form .submit_p {
  text-align: center;
}
.login_form .submit_p .submit_bth {
  height: 40px;
  line-height: 40px;
  width: 347px;
  cursor: pointer;
  color: #fff;
  background-color: #409eff;
  border-color: #409eff;
  border: 1px solid #dcdfe6;
  -webkit-appearance: none;
  text-align: center;
  box-sizing: border-box;
  outline: none;
  margin: 0;
  transition: 0.1s;
  font-weight: 500;
  -moz-user-select: none;
  -webkit-user-select: none;
  -ms-user-select: none;
  font-size: 14px;
  border-radius: 4px;
  margin: 10px 0;
}
.login_form .submit_p .submit_bth:hover {
  background: #3c84ce;
}
.login_form p span {
  font-size: 16px;
}
.login_form p input {
  width: 293px;
  -webkit-appearance: none;
  background-color: #fff;
  background-image: none;
  border-radius: 4px;
  border: 1px solid #dcdfe6;
  box-sizing: border-box;
  color: #606266;
  font-size: 14px;
  display: inline-block;
  height: 35px;
  line-height: 35px;
  outline: none;
  padding: 0 15px;
  transition: border-color 0.2s cubic-bezier(0.645, 0.045, 0.355, 1);
}
.login_form p input:focus {
  outline: none;
  border-color: #409eff;
}

/* 表单结束 */
</style>





