<template>
  <div class="data_upload">
    <div class="top_search">
      <span class="logo_text">sitemap</span>
      <span class="search_content">
        <div>
          <button class="search_btn" @click="searchRote">
            <img src="@/assets/images/search.png" />
          </button>
          <button class="logout_btn" @click="logout">
            <span class="logout">登出</span>
          </button>
        </div>
      </span>
    </div>
    <div class="upload_ul">
      <ul>
        <li>
          <p class="upload_title">数据匹配</p>
          <p class="upload_button">
            <span class="upload_click">上传文件</span>
            <input type="file" class="file_input" @change="getfilename($event)" />
          </p>
        </li>
        <li>
          <p class="upload_title">数据更新</p>
          <p class="upload_button">
            <span class="upload_click">上传文件</span>
            <input type="file" class="file_input" @change="getfilename($event)" />
          </p>
        </li>
        <li>
          <p class="upload_title">数据上传</p>
          <p class="upload_button">
            <span class="upload_click">上传文件</span>
            <input type="file" class="file_input" @change="getfilename($event)" />
          </p>
        </li>
      </ul>
    </div>
    <!-- 进度条开始 -->
    <div>
      <div class="stepDiv">
        <div class="step_main stepOne step_active">
          <div>开始</div>
          <span class="right_span"></span>
        </div>
        <div class="step_main stepTwo">
          <span class="left_span"></span>
          <div>进行中</div>
          <span class="right_span"></span>
        </div>
        <div class="step_main stepThree">
          <span class="left_span"></span>
          <div>完成</div>
          <span class="right_span"></span>
        </div>
        <div class="view_results" @click="showResults()">查看结果</div>
      </div>
    </div>
    <!-- 进度条结束 -->

    <!-- 提示开始 -->
    <div class="notice_ul">
      <ul>
        <li>1、确保文件格式为.csv</li>
        <li>2、文件里的字段名正确</li>
        <li>3、数据上传前需要做好人工审查</li>
      </ul>
    </div>
    <!-- 提示结束 -->

    <!-- 查看结果弹出层开始 -->
    <div class="show_results" v-show="resultIsShow">
      <div class="results">
        <i @click="closeResults()">
          <img src="@/assets/images/pic.png" />
        </i>
        <h2>匹配结果</h2>
        <div class="success_results">
          <p class="results_title">匹配成功数据</p>
          <p class="result_notice">
            <span>共匹配成功111条</span>
            <span class="download_file">下载</span>
          </p>
        </div>
        <div class="unmatch_results">
          <p class="results_title">未匹配到数据</p>
          <p class="result_notice">
            <span>未匹配数据125条</span>
            <span class="download_file">下载</span>
          </p>
        </div>
      </div>
    </div>
    <!-- 查看结果弹出层结束 -->
  </div>
</template>
<script>
import { logout } from "@/api";
export default {
  name: "DataUpload",
  data() {
    return {
      resultIsShow: false
    };
  },
  methods: {
    logout() {
      let vm = this;
      let token = vm.$commonTools.getCookie("user_token");
      let newToken = token.replace('"', "").replace('"', "");

      logout(newToken)
        .then(function(response) {
          console.log(response);
          if (response.status === 204) {
            console.log("204");
            vm.$commonTools.delCookie("user_token");
            vm.$router.push({ path: "/" });
            // vm.$router.go(0);
          } else {
            console.log("非204");
          }
        })
        .catch(function(error) {
          console.log(error);
          console.log("登出，请重试！");
        });
    },
    searchRote() {
      // this.$router.push({ path: "/search" });
      this.$router.push({ path: "/mysearch" });
    },
    showResults() {
      this.resultIsShow = true;
    },
    closeResults() {
      this.resultIsShow = false;
    },
    getfilename(e) {
      let file = e.target.files;
      console.log(file);
    }
  }
};
</script>
<style scoped>
.data_upload {
  width: 75%;
  margin: 0 auto;
  min-width: 1200px;
}
.data_upload .top_search {
  margin: 0 auto;
  overflow: hidden;
  height: 10vh;
  line-height: 10vh;
}
.data_upload .top_search .logo_text {
  color: #0e63af;
  display: block;
  font-size: 30px;
  font-weight: 700;
  float: left;
}
.data_upload .top_search .search_content {
  float: right;
}
.data_upload .search_btn {
  cursor: pointer;
  border: 1px solid #f0f0f0;
  padding: 4px 25px;
  border-radius: 3px;
  margin-top: 10px;
  vertical-align: top;
}
.data_upload .search_btn:hover {
  border: 1px solid #409eff;
  background-color: #fff;
}

.data_upload .logout_btn {
  /* position: relative; */
  margin-left: 35px;
  cursor: pointer;
  height: 36px;
  background-color: red;
  width: 80px;
  color: white;
  border: 0px;
  font-size: 16px;
  vertical-align: top;
  margin-top: 10px;
}
.data_upload .logout_btn:hover {
  height: 40px;
  background-color: rgb(231, 164, 164);
  width: 80px;
  color: white;
}

/* 数据上传开始 */
.data_upload .upload_ul {
  margin-top: 20px;
}
.data_upload .upload_ul ul {
  /* width: 100%; */
  overflow: hidden;
}
.data_upload .upload_ul ul li {
  border: 1px solid #f0f0f0;
  border-radius: 2px;
  list-style: none;
  float: left;
  width: 30%;
  text-align: center;
  padding: 50px 0px;
  margin: 3px 20px 3px 0px;
  border: 1px solid #f0f0f0;
  -moz-box-shadow: 0px 0px 7px #f0f0f0;
  -webkit-box-shadow: 0px 7px 5px #f0f0f0;
  box-shadow: 0px 0px 7px #f0f0f0;
}
.data_upload .upload_ul ul li p {
  height: 50px;
  line-height: 50px;
}
.data_upload .upload_ul ul li .upload_title {
  font-size: 24px;
  font-weight: 700;
  margin-bottom: 35px;
}
.data_upload .upload_button {
  position: relative;
}
.data_upload .upload_click {
  display: block;
  width: 130px;
  height: 36px;
  background: #409eff;
  color: #fff;
  text-align: center;
  line-height: 36px;
  margin: 0 auto;
  cursor: pointer;
  border-radius: 3px;
}
/* .data_upload .upload_click:hover {
  background: #3c84ce;
} */
.data_upload .file_input {
  width: 200px;
  height: 36px;
  position: absolute;
  left: 100px;
  top: 5px;
  z-index: 1;
  -moz-opacity: 0;
  -ms-opacity: 0;
  -webkit-opacity: 0;
  opacity: 0;
  filter: alpha(opacity=0);
  cursor: pointer;
}
/* 数据上传结束 */

/* 流程步骤开始 */
.stepDiv {
  height: 50px;
  line-height: 50px;
  margin: 50px 0px 50px 40px;
}
.step_main {
  display: block;
  font-size: 0;
  position: relative;
  float: left;
}
.step_main div {
  display: inline-block;
  background: #f8f8f8;
  height: 50px;
  vertical-align: text-bottom;
  font-size: 15px;
  color: #333;
  line-height: 50px;
  text-align: center;
}
.stepOne div {
  width: 300px;
}
.stepTwo div {
  width: 250px;
}
.stepThree div {
  width: 250px;
}
.step_main.step_active div {
  background: #ff5837;
  color: #fff;
}
.step_main .right_span {
  display: inline-block;
  width: 0;
  height: 0;
  border-width: 25px 0 25px 20px;
  border-style: solid;
  border-color: transparent transparent transparent #f8f8f8;
}
.step_main.step_active .right_span {
  border-color: transparent transparent transparent #ff5837;
}
.step_main p {
  position: absolute;
  color: #fff;
}
.step_main .left_span {
  display: block;
  float: left;
  border-width: 25px 25px 25px 25px;
  border-style: solid;
  border-color: #f8f8f8 #f8f8f8 #f8f8f8 transparent;
  top: 0px;
  left: 0px;
}
.step_main.step_active .left_span {
  border-color: #ff5837 #ff5837 #ff5837 transparent;
}
.view_results {
  float: left;
  margin-left: 30px;
  border: 1px solid #f0f0f0;
  cursor: pointer;
  background: #409eff;
  height: 40px;
  line-height: 40px;
  padding: 2px 25px;
  border-radius: 3px;
  color: #fff;
}
.view_results:hover {
  background: #3c84ce;
}
/* 步骤流程结束 */

/* 提示内容开始 */
.notice_ul ul li {
  font-size: 16px;
  list-style: none;
  height: 35px;
  line-height: 35px;
}
/* 提示内容结束*/

/* 匹配数据弹出层开始 */
.show_results {
  height: 100vh;
  width: 100vw;
  position: absolute;
  top: 0px;
  left: 0px;
  background-color: rgba(0, 0, 0, 0.6);
  z-index: 10;
}
.show_results .results {
  border-radius: 5px;
  width: 800px;
  margin: 200px auto;
  background: #fff;
  padding-bottom: 30px;
}
.show_results .results i {
  float: right;
  width: 25px;
  height: 25px;
  cursor: pointer;
}
.show_results .results i img {
  width: 100%;
}
.show_results .results h2 {
  text-align: center;
  width: 100%;
  background: #f0f0f0;
  height: 50px;
  line-height: 50px;
  border-radius: 5px;
}
.show_results .results .success_results {
  border-bottom: 1px dashed #666;
}
.show_results .results .success_results,
.show_results .results .unmatch_results {
  padding: 20px;
}
.success_results p.results_title,
.unmatch_results p.results_title {
  font-size: 18px;
  color: #333;
  margin-bottom: 40px;
}
.show_results .result_notice {
  text-align: center;
  color: #666;
}
.show_results .result_notice .download_file {
  margin-left: 100px;
  border-radius: 3px;
  background: #409eff;
  cursor: pointer;
  color: #fff;
  padding: 5px 25px;
}
.show_results .result_notice .download_file:hover {
  background: #3c84ce;
}
/* 匹配数据弹出层结束 */
</style>





