import Vue from 'vue'
import Router from 'vue-router'
import Login from '@/views/login/Login'
import DataUpload from '@/views/dataUpload/DataUpload'
import Search from '@/views/search/Search'
import MySearch from '@/views/mysearch/mysearch'


Vue.use(Router)

export default new Router({
  mode: 'history',
  routes: [
    {
      path: '/',
      name: 'Login',
      component: Login,
      meta: {
        title: '登录页',
        requiresAuth: false,
        roleId: 1,
      }
    },
    {
      path: '/dataUpload',
      name: 'DataUpload',
      component: DataUpload,
      meta: {
        title: '数据上传',
        requiresAuth: true,
        roleId: 1,
      }
    },
    {
      path: '/search',
      name: 'Search',
      component: Search,
      meta: {
        title: '搜索数据',
        requiresAuth: true,
        roleId: 1,
      }
    },
    {
      path: '/mysearch',
      name: 'Mysearch',
      component: MySearch,
      meta: {
        title: '我的搜索数据',
        requiresAuth: true,
        roleId: 1,
      }
    }
  ]
})



