<template>
  <div class="data_search">
    <div class="top_search">
      <span class="logo_text" @click="goIndex">sitemap</span>
      <span class="search_content">
        <input type="text" v-model="searchContent" />
        <button class="search_btn" @click="submitSearch">搜索</button>
      </span>
    </div>

    <!-- 搜索到的数据开始 -->
    <div class="search_matching">
      <div class="message_notice">{{messageNotice}}</div>
      <div class="search_list" v-show="searchList !=null">
        <!-- <ul>
          <span class="data_title">Id</span>
          <span class="data_content">公司</span>
          <span class="data_date">法人</span>
          <span class="data_date">算分</span>
          <li v-for="(list,index) in searchList" :key="index" @click="showDetail(list)">
            <span class="data_title">{{list._source.id}}</span>
            <span class="data_content">{{list._source.account_name}}</span>
            <span class="data_date">{{list._source.legal_person}}</span>
            <span class="data_date">{{list._score}}</span>
          </li>
        </ul> -->
        <el-table :data="searchList" stripe style="width: 100%">
          <el-table-column prop="_source.id" label="Id" width="180"></el-table-column>
          <el-table-column prop="_source.account_name" label="公司"></el-table-column>
          <el-table-column prop="_source.legal_person" label="法人" width="180"></el-table-column>
          <el-table-column prop="_score" label="算分"></el-table-column>
        </el-table>
      </div>
    </div>
    <!-- 搜索到的数据结束 -->

    <!-- <input type="text" v-model="searchContent2" /> -->
    <!-- <button class="search_btn" @click="submitSearch2">获取</button> -->

    <!-- 点击查看数据详情开始 -->
    <div class="data_detail" v-if="listDetail !=null">
      <div class="data_detail_content">
        <i @click="closeResults()">
          <img src="@/assets/images/pic.png" />
        </i>
        <ul>
          <li>
            <span class="detail_title">Id：</span>
            <span>{{listDetail._source.id}}</span>
          </li>
          <li>
            <span class="detail_title">公司：</span>
            <span>{{listDetail._source.account_name}}</span>
          </li>
          <li>
            <span class="detail_title">法人：</span>
            <span>{{listDetail._source.legal_person}}</span>
          </li>
          <li>
            <span class="detail_title">算分：</span>
            <span>{{listDetail._score}}</span>
          </li>
        </ul>
      </div>
    </div>
    <!-- 点击查看数据详情结束 -->
  </div>
</template>
<script>
import { searchCompany, searchCompanyById } from "@/api";

export default {
  name: "MySearch",
  data() {
    return {
      detailIsShow: false, //默认弹出详情不显示
      searchContent: "",
      // searchContent2: "",
      messageNotice: "",
      searchList: null,
      searchData: [
        // {
        //   title: "数据一",
        //   content: "测试数据测试数据测试数据",
        //   date: "2019-02-05"
        // },
        // {
        //   title: "数据二",
        //   content: "测试数据测试数据测试数据",
        //   date: "2019-01-20"
        // },
        // {
        //   title: "数据三",
        //   content: "测试数据测试数据测试数据",
        //   date: "2019-04-30"
        // },
        // {
        //   title: "数据四",
        //   content: "测试数据测试数据测试数据",
        //   date: "2019-07-01"
        // },
        // {
        //   title: "数据五",
        //   content: "测试数据测试数据测试数据",
        //   date: "2019-01-05"
        // },
        // {
        //   title: "数据六",
        //   content: "测试数据测试数据测试数据",
        //   date: "2019-04-12"
        // },
