        // {
        //   title: "数据八",
        //   content: "测试数据测试数据测试数据",
        //   date: "2019-05-05"
        // },
        // {
        //   title: "数据九",
        //   content: "测试数据测试数据测试数据",
        //   date: "2019-02-25"
        // }
      ],
      listDetail: null,
      data: null
    };
  },
  methods: {
    // submitSearch2() {
    //   if (this.searchContent == "") {
    //     this.messageNotice = "请输入搜索数据关键字！";
    //   } else {
    //     let id = vm.searchContent2;
    //     searchCompanyById(id)
    //       .then(function(response) {
    //         // console.log(response);
    //         // console.log(response.data);
    //         // console.log(response.data.hits);
    //         // console.log(response.data.hits.hits);
    //         // console.log(response.data.hits.hits[0]);
    //         // console.log(response.data.hits.hits[0]);
    //         // console.log(response.data.hits.hits[0]._source);
    //         // console.log(response.data.hits.hits[0]._source.id);
    //         // console.log(response.data.hits.hits[0]._source.account_name);
    //         // console.log(response.data.hits.hits[0]._source.legal_person);
    //         // let length = response.data.hits.hits.length;
