    //         // for (i = 0; i < length; i++) {
    //         //   vm.searchData.push(response.data.hits.hits[i]);
    //         // }
    //       })
    //       .catch(function(error) {
    //         console.log(error);
    //       });
    //   }
    // },
    submitSearch() {
      let vm = this;

      if (this.searchContent == "") {
        this.messageNotice = "请输入搜索数据关键字！";
      } else {
        this.messageNotice = "搜索到的数据为...";
        this.searchList = this.searchData;

        let token = vm.$commonTools.getCookie("user_token");
        let newToken = token.replace('"', "").replace('"', "");
        console.log(newToken);

        var formData = new FormData();
        var formData = new window.FormData();
        formData.append("search", vm.searchContent);
        vm.data = formData;
        console.log(newToken);

        searchCompany(newToken, vm.data)
          .then(function(response) {
            console.log(response);
            // console.log(response.data);
            // console.log(response.data.hits);
            // console.log(response.data.hits.hits);
            // console.log(response.data.hits.hits[0]);
            console.log(response.data.hits.hits[0]);
            console.log(response.data.hits.hits[0]._source);
            // console.log(response.data.hits.hits[0]._source.id);
            // console.log(response.data.hits.hits[0]._source.account_name);
            // console.log(response.data.hits.hits[0]._source.legal_person);
            let length = response.data.hits.hits.length;
            let i;
            for (i = 0; i < length; i++) {
              vm.searchData.push(response.data.hits.hits[i]);
            }
          })
          .catch(function(error) {
            console.log(error);
          });
      }
    },
    showDetail(list) {
      this.listDetail = list;
      // this.detailIsShow = true
      console.log(this.listDetail);
    },
    //点击弹框右侧关闭按钮关闭详情
    closeResults() {
      this.listDetail = null;
    },
    //点击logo跳转首页
    goIndex() {
      this.$router.push({ path: "/" });
    }
  }
};
