<style scoped>

.data_search {
  width: 75%;
  margin: 0 auto;
  min-width: 1200px;
}
.top_search {
  margin: 0 auto;
  overflow: hidden;
  height: 10vh;
  line-height: 10vh;
}

.search_matching .message_notice {
  color: #666;
}
.top_search .logo_text {
  color: #0e63af;
  display: block;
  font-size: 30px;
  font-weight: 700;
  float: left;
  cursor: pointer;
}
.top_search .search_content {
  float: right;
}
.top_search .search_content input {
  width: 400px;
  -webkit-appearance: none;
  background-color: #fff;
  background-image: none;
  border-radius: 4px;
  border: 1px solid #dcdfe6;
  box-sizing: border-box;
  color: #606266;
  font-size: 14px;
  display: inline-block;
  height: 35px;
  line-height: 35px;
  outline: none;
  padding: 0 15px;
  transition: border-color 0.2s cubic-bezier(0.645, 0.045, 0.355, 1);
}
.top_search .search_content input:focus {
  outline: none;
  border-color: #409eff;
}
.top_search .search_content .search_btn {
  height: 37px;
  line-height: 35px;
  width: 100px;
  cursor: pointer;
  color: #fff;
  background-color: #409eff;
  border: 1px solid #dcdfe6;
  -webkit-appearance: none;
  text-align: center;
  box-sizing: border-box;
  outline: none;
  margin: 0;
  transition: 0.1s;
  font-weight: 500;
  -moz-user-select: none;
  -webkit-user-select: none;
  -ms-user-select: none;
  font-size: 14px;
  border-radius: 4px;
  margin: 10px 0;
}
.top_search .search_content .search_btn:hover {
  background: #3c84ce;
}
.search_list ul {
  border: 1px solid #f0f0f0;
  padding: 0;
  border-top: none;
}
.search_list ul li {
  padding: 8px 10px;
  list-style: none;
  border-top: 1px solid #f0f0f0;
  cursor: pointer;
}
.search_list ul li:hover {
  background: #68b2ff;
}
.search_list ul li .data_title {
  margin-right: 10px;
  color: #333;
  font-size: 16px;
}
.search_list ul li .data_content {
  color: #666;
  font-size: 14px;
}
.search_list ul li .data_date {
  color: #666;
  font-size: 12px;
  margin-left: 10px;
}

/* 数据详情开始 */
.data_detail {
  height: 100vh;
  width: 100vw;
  position: absolute;
  top: 0px;
  left: 0px;
  background-color: rgba(0, 0, 0, 0.6);
  z-index: 10;
}
.data_detail .data_detail_content ul {
  padding: 30px;
}
.data_detail .data_detail_content ul li {
  font-size: 16px;
  list-style: none;
  height: 45px;
  line-height: 45px;
  border-bottom: 1px solid #f0f0f0;
  padding-left: 5px;
  color: #666;
}
.data_detail .data_detail_content ul li .detail_title {
  font-size: 18px;
  font-weight: 500;
  color: #333;
}
.data_detail .data_detail_content {
  border-radius: 5px;
  width: 800px;
  margin: 200px auto;
  background: #fff;
  padding-bottom: 10px;
}
.data_detail .data_detail_content i {
  float: right;
  width: 25px;
  height: 25px;
  cursor: pointer;
}
.data_detail .data_detail_content i img {
  width: 100%;
}
/* 数据详情结束 */
</style>





